
.main-section {
  height: 100vh;
  background-image: url(../images/Bez-nazwy-21.png);
  background-position: center;
  background-size: cover;
  position: relative;
  
  @media (max-width: 780px) {
    height: unset;
  }
}



.main-header {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 20px;
  position: relative;
  z-index: 2;
  
  @media (max-width: 732px) {
    flex-direction: column;
  }
}

.contact {
  display: flex;
  align-content: center;
  
  @media (max-width: 520px) {
    flex-direction: column;
  }
}
.contact__wrapper {
  margin-left: 30px;
  display: flex;
  align-items: center;
  @media (max-width: 520px) {
    margin-left: 0;
    margin-bottom: 10px;
  }
}
.contact__tel {
  font-weight: lighter;
  text-decoration: none;
  color: #405565;
}
.contact__mail {
  font-weight: lighter;
  text-decoration: none;
  color: #405565;
  word-break: break-all;
}
.contact__tel-icon {
  margin-right: 5px;
}



//main-banner
.main-banner {
  text-align: center;
  margin-top: 80px;
}

.title {
  font-size: 40px;
  font-weight: bold;
  color: $mainColor;
  text-transform: uppercase;
  margin-bottom: 20px;
  
  @media (max-width: 520px) {
    font-size: 28px;
  }
  
  @media(min-width: 1336px) {
    margin-bottom: 40px;
    font-size: 50px;
  }
}

.title__desc {
  color: $descColor;
  font-weight: lighter;
  font-size: 24px;
  margin-bottom: 30px;
  @media (max-width: 520px) {
    font-size: 20px;
  }
  @media(min-width: 1336px) {
    margin-bottom: 50px;
    font-size: 34px;
  }
}

.contact-link {
  display: inline-block;
  text-decoration: none;
  padding: 8px 160px;
  background-color: #f82e46;
  text-transform: uppercase;
  margin-bottom: 10px;
  border-radius: 30px;
  color: $mainColor;
  font-weight: bold;
  
  @media(min-width: 1600px) {
   padding: 16px 232px;
   font-size: 23px;
  }
  @media(min-width: 1336px) {
    font-size: 19px;
    padding: 10px 218px;
   }
  @media (max-width: 702px) {
    padding: 8px 140px;
  }
  @media (max-width: 615px) {
    padding: 8px 110px;
  }
  @media (max-width: 502px) {
    padding: 8px 75px;
  }
  @media (max-width: 400px) {
    padding: 8px 35px;
  }
}

.link-desc {
  font-weight: lighter;
  font-size: 14px;
  color: $descColor;
  @media(min-width: 1600px) {
    font-size: 16px;
   }
  @media(min-width: 1336px) {
    font-size: 16px;
   }
}

.company {
  margin-top: 60px;
  
  @media(min-width: 1336px) {
    margin-top: 100px;
  }
}

.company-services {
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  align-items: center;
  text-align: center;
}

.company-services__icon {
  height: 63px;
  margin-bottom: 10px;
  @media(min-width: 1336px) {
    margin-bottom: 20px;
  }
}
.company-services__img {}
.company-services__desc {
  max-width: 255px;
  color: $mainColor;
  word-spacing: 2px;
  line-height: 1.4;
  @media(min-width: 1336px) {
    
  }
}

.company-services__title {
  font-size: 20px;
  color: $mainColor;
  margin-bottom: 10px;
  word-spacing: 4px;
  font-weight: bold;
  
  @media(min-width: 1336px) {
    
  }
}

.company {
  @media (max-width: 778px) {
    flex-direction: column;
    padding-bottom: 50px;
  }
}

.company_item {}